.dot {
    height: 10px;
    width: 10px;
    background-color: transparent;
    border-width: 2px;
    border-radius: 50%;
    border: 2px solid #EE719D;
    display: inline-block;
    margin: 0 1px;
}

.dot.active {
    background-color: #EE719D;
}


.header-title {
    font-size: 18px;
    text-overflow:ellipsis; 
}